<template>
  <div id="transactionInquiry">
    <!-- Start Time -->
    <section class="prlmaryone">
     <div>{{$t("message.transferPayment.transactionInquiry.startdate")}}</div>
     <input v-model="selectTime.startTime" :style="changeStyleOne" :placeholder="$t('message.transferPayment.transactionInquiry.enterStart')" readonly="readonly">
     <i class="transactionInquiry_calendar" @click="selectTimes(selectTime[0])" ></i>
    </section>
    <!-- End Time -->
    <section class="prlmaryone" style="padding: 0.2rem 0.3rem 0 0.3rem;">
     <div>{{$t("message.transferPayment.transactionInquiry.deadline")}}</div>
     <input v-model="selectTime.endTime" :style="changeStyleTwo" :placeholder="$t('message.transferPayment.transactionInquiry.enterEnd')" readonly="readonly">
     <i class="transactionInquiry_calendar" style="top:2.4rem" @click="selectTimes(selectTime[1])" ></i>
    </section>
    <!-- filtrate -->
    <section class="prlmaryone" style="padding: 0.2rem 0.3rem 0 0.3rem;">
     <div>{{$t("message.transferPayment.transactionInquiry.filtrate")}}</div>
     <input  v-model="refer" class="refer" style="width:50%">
     <div style="height:0.7rem;width:25%;font-size:0.26rem;line-height:0.7rem;text-align:center;">
       <div style="background-color:#5AC4CF;width:85%;height:100%;margin-left:0.2rem;border-radius: 0.1rem;color:#fff;" @click="SeeMore">{{$t("message.transferPayment.transactionInquiry.refer")}}</div>
    </div>
    </section>
    <!-- Deal list -->
    <section style=" padding: 0.2rem 0.3rem 0 0.3rem;" >
      <div class="listStyle">
        <span>{{$t("message.transferPayment.transactionInquiry.date")}}</span>
        <span>{{$t("message.transferPayment.walletAddress")}}</span>
        <span style="padding:0 0.3rem 0 0 ">{{$t("message.transferPayment.remittanceAmount")}}</span>
      </div>
      <ul>
        <li v-for="(item,index) in tradingRecord" :key="index" class="listStyle" style="padding:0.2rem 0">
          <div style="display:flex;justify-content: space-between;width:100%" @click="billingDetails(item)">
            <span>{{item.time}}</span>
            <span>{{item.walletAddress}}</span>
            <span style="padding:0 0.3rem 0 0 ">{{item.money}}</span>
            <i class="personal_rightArrow"></i>
        </div>
        </li>
      </ul>
    </section>
    <!-- dateTime -->
    <section>
      <van-popup v-model="show">
        <div style="width:600px;height:300px;font-size:0.26rem">
          <van-datetime-picker style="font-size:0.3rem" v-model="currentDate" type="date" :min-date="minDate" 
          :item-height="50" @confirm="confirm(excessive)" @cancel="cancel" 
          :confirm-button-text="$t('message.transferPayment.transactionInquiry.confirm')"
          :cancel-button-text="$t('message.transferPayment.transactionInquiry.cancel')"/>
        </div>
      </van-popup>
    </section>
    <!-- Billing Details -->
    <section >
      <van-popup v-model="popup" position="left" :overlay="false">
          <div :style="fullscreen">
            <div class="top" >
              <img src="../../images/language_left.png" @click="goback"> 
              <span>{{$t("message.transferPayment.transactionInquiry.billingDetails")}}</span>
            </div>
            <div class="BillingDetails">
              <span>{{BillingDetails.indentNumber.name}}</span><span>{{BillingDetails.indentNumber.value}}</span>
            </div>
            <div class="BillingDetails">
              <span>{{BillingDetails.remittanceAmount.name}}</span><span>{{BillingDetails.remittanceAmount.value}}</span>
            </div>
            <div class="BillingDetails">
              <span>{{BillingDetails.transactionCost.name}}</span><span>{{BillingDetails.transactionCost.value}}</span>
            </div>
            <div class="BillingDetails">
              <span>{{BillingDetails.exchangeRate.name}}</span><span>{{BillingDetails.exchangeRate.value}}</span>
            </div>
            <div class="BillingDetails">
              <span>{{BillingDetails.receivableAmount.name}}</span><span>{{BillingDetails.receivableAmount.value}}</span>
            </div>
            <div class="BillingDetails">
              <span>{{BillingDetails.payee.name}}</span><span>{{BillingDetails.payee.value}}</span>
            </div>
            <div class="BillingDetails">
              <span>{{BillingDetails.walletAddress.name}}</span><span>{{BillingDetails.walletAddress.value}}</span>
            </div>
            <div class="BillingDetails">
              <span>{{BillingDetails.purpose.name}}</span><span>{{BillingDetails.purpose.value}}</span>
            </div>
            <div class="BillingDetails">
              <span>{{BillingDetails.result.name}}</span><span>{{BillingDetails.result.value}}</span>
            </div>
            <div class="BillingDetails">
              <span>{{BillingDetails.date.name}}</span><span>{{BillingDetails.date.value}}</span>
            </div>
          </div>
      </van-popup>
    </section>
    <section></section>
  </div>
</template>
<script>
export default {
  name: "transactionInquiry",
  data() {
    return {
      excessive: "",
      show: false,
      popup: false,
      refer: "",
      minDate: new Date(2018, 10, 1),
      maxDate: new Date(),
      currentDate: new Date(),
      changeDate: "",
      selectTime: [
        {
          startTime: "",
          startTimeTip: "1"
        },
        {
          endTime: "",
          endTimeTip: "2"
        }
      ],
      tradingRecord: [
        {
          time: "2019-2-3",
          walletAddress: "sssss",
          money: "3456765432"
        },
        {
          time: "2019-2-4",
          walletAddress: "sssss",
          money: "3456765432"
        },
        {
          time: "2019-2-5",
          walletAddress: "sssss",
          money: "3456765432"
        }
      ],
      BillingDetails: {
        indentNumber: {
          name: this.$t(
            "message.transferPayment.transactionInquiry.indentNumber"
          ),
          value: "11345456432"
        },
        remittanceAmount: {
          name: this.$t("message.transferPayment.remittanceAmount"),
          value: "13,245 GSD"
        },
        transactionCost: {
          name: this.$t("message.transferPayment.transactionCost"),
          value: "5 GSD"
        },
        exchangeRate: {
          name: this.$t("message.transferPayment.exchangeRate"),
          value: "5,791.2 HKD"
        },
        receivableAmount: {
          name: this.$t("message.transferPayment.receivableAmount"),
          value: "5,786.2 HKD"
        },
        payee: {
          name: this.$t("message.transferPayment.payee"),
          value: "赵四"
        },
        walletAddress: {
          name: this.$t("message.transferPayment.walletAddress"),
          value: "sdjfiojwqiHIJnjknJDJBjbk"
        },
        purpose: {
          name: this.$t("message.transferPayment.purpose"),
          value: this.$t("message.transferPayment.overseasInvestment")
        },
        result: {
          name: this.$t("message.transferPayment.transactionInquiry.result"),
          value: this.$t(
            "message.transferPayment.transactionInquiry.paymentSuccess"
          )
        },
        date: {
          name: this.$t("message.transferPayment.transactionInquiry.date"),
          value: "2019-02-18"
        }
      }
    };
  },
  computed: {
    changeStyleOne: function() {
      if (this.selectTime[0].startTime !== this.changeDate) {
        return "border-color:#A5C3FA;color:#373e44";
      }
    },
    changeStyleTwo: function() {
      if (this.selectTime[1].endTime !== this.changeDate) {
        return "border-color:#A5C3FA;color:#373e44";
      }
    },
    fullscreen: function() {
      return {
        width: screen.width * window.devicePixelRatio + "px",
        height: screen.height * window.devicePixelRatio + "px",
        background: "#fafafa"
      };
    }
  },
  methods: {
    selectTimes(item) {
      if (this.selectTime[0].startTimeTip === item.startTimeTip) {
        this.show = true;
        this.excessive = "0";
        console.log("走开始日期");
      } else {
        this.show = true;
        this.excessive = "1";
        console.log("走结束日期");
      }
    },
    // 日期选择确认按钮
    confirm(excessive) {
      console.log(excessive);
      if (excessive === "0") {
        this.change();
        this.selectTime.startTime = this.changeDate;
      } else {
        this.change();
        this.selectTime.endTime = this.changeDate;
      }
      this.show = false;
    },
    // 日期选择取消
    cancel() {
      console.log("取消");
      this.show = false;
    },
    //修改时间格式
    change() {
      var date = this.currentDate;
      var seperator1 = "-";
      var seperator2 = ":";
      var month = date.getMonth() + 1;
      var strDate = date.getDate();
      if (month >= 1 && month <= 9) {
        month = "0" + month;
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = "0" + strDate;
      }
      this.changeDate =
        date.getFullYear() + seperator1 + month + seperator1 + strDate;
    },
    SeeMore() {
      this.selectTime.startTime = "";
      this.selectTime.endTime = "";
      console.log("查询");
    },
    billingDetails(item) {
      this.popup = true;
    },
    goback() {
      this.popup = false;
    }
  }
};
</script>
<style lang="scss" scoped>
.prlmaryone {
  padding: 1.28rem 0.3rem 0 0.3rem;
  text-align: left;
  display: flex;
  div {
    height: 0.7rem;
    width: 25%;
    font-size: 0.26rem;
    color: #373e44;
    line-height: 0.7rem;
  }
  input {
    height: 0.7rem;
    width: 75%;
    font-size: 0.26rem;
    color: #bdbebf;
    padding: 0 0.2rem;
    border: 0.01rem solid #bdbebf;
    border-radius: 0.1rem;
  }
}
.listStyle {
  border-bottom: 1px solid #dadfe3;
  display: flex;
  font-size: 0.3rem;
  color: #555b61;
  justify-content: space-between;
}
.refer {
  height: 0.7rem;
  font-size: 0.26rem;
  color: #bdbebf;
  border: 0.01rem solid #bdbebf;
  border-radius: 0.1rem;
}
.top {
  width: 100%;
  height: 0.88rem;
  text-align: center;
  line-height: 0.8rem;
  font-size: 0.3rem;
  color: #fff;
  background-color: #005c9c;
  img {
    width: 0.24rem;
    height: 0.36rem;
    position: absolute;
    left: 0.2rem;
    top: 0.24rem;
  }
}
.BillingDetails {
  padding: 0 0.2rem;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #ebebed;
  font-size: 0.26rem;
  color: #5b636a;
  span {
    padding: 0.2rem 0;
  }
}
</style>



